.App {
  text-align: center;
  font-family: sans-serif;
  
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #666;
}

li {
  float: left;
}

.right {
  float: right;
  margin-right: 80px ;
}
.first {
  margin-left: 80px;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
  background-color: #111;
}


.coin {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: auto;
  max-width: 1200px;
  
}
.item {
  width: 240px;
  margin-top: 2%;
  border-radius: 20px;
  background-image: linear-gradient(-45deg,  lightgray, #B9F3FC);
  text-align: left;
  box-shadow: 3px 3px #B9F3FC;
}
select{
  border-radius: 10px;
  width: 12%;
  height: 3rem;
  font-size: 2rem;
  display: inline;
  margin: 1% 1%;
  font-family: sans-serif;
  cursor: pointer;
}
.mainBtn {
  border-radius: 8px;
  width: 8%;
  font-size: 1.3rem;
  display: inline;
  margin: 27px 30px;
  font-family: sans-serif;
  cursor: pointer;
}
.SecondBtn {
  border-radius: 8px;
  border: 1px solid gray;
  font-size: 1rem;
  cursor: pointer;
  margin-left: 30%;
  margin-right: auto;
}
h3 {
  font-size: 1rem;
}

@media only screen and (max-width: 600px) {
  select {
    width: 100px;
    font-size: 1rem;
  }
  .mainBtn{
    width: 80px;
  }
  li {
    font-size: 1rem;
  }
  .right {
    float: right;
    margin-right: 15px ;
  }
  .first {
    margin-left: 0;
  }
}